import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ChannelManagementBean } from './../bean/channel-management.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';

@Component({
  selector: 'app-channel-management-add',
  templateUrl: './channel-management-add.component.html',
  styleUrls: ['./channel-management-add.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 * 渠道站点-站点配置-新增
 */
export class ChannelManagementAddComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  // bean
  channelManagementBean: ChannelManagementBean = new ChannelManagementBean();
  //提示信息
  msgs: Message[] = [];
  userform: FormGroup; //校验
  siteType: any;//站点类型
  code: any;
  iscompele: boolean = false;
  channelId1: string = 'CHNL';
  channelId2: string = '';

  constructor(public productManageHttpService: ProductManageHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc, ) {
    this.userform = fb.group({
      channelId: new FormControl('', [Validators.pattern(/^[a-zA-z]{2}[0-9]{6}$/), Validators.required]),
      // channelId1: ['', Validators.required],
      // channelId2: new FormControl('', [Validators.pattern(/^[0-9]\d*$/), Validators.required]),
      channelName: ['', Validators.required],
      channelDescription: [''],
      serverCode: [''],
      systemCode: [''],
      situationCode: [''],
      channelType: ['', Validators.required],
    });
    this.codeValues(); // 调用方法，获取全部码值
    this.siteType = this.code['SiteType'];
    this.siteType.unshift({ 'label': '请选择', 'Value': '' });
    this.siteType = _.clone(this.siteType);
  }

  ngOnInit() {
    // this.getSeqNo();
  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }
  // 获取默认编号
  getSeqNo() {
    let parms = {
      seqType: 'CHANNEL',
    };
    this.productManageHttpService.getSeqNo(parms).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.channelId2 = data.seqNo;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  //添加
  addSaveBtn() {
    this.iscompele = true;
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        this.iscompele = false;
        return;
      }
    }
    // let channelId2 = this.channelId2.split('');
    // if (channelId2.length == 6) {
    //   this.channelManagementBean.channelId = this.channelId1 + this.channelId2;
      // 提交服务
      this.productManageHttpService.productChannelCreate(this.channelManagementBean).subscribe(data => {
        if (data.returnCode.code == "success") {
          this.channelManagementBean = new ChannelManagementBean();
          let parm = {
            display: false,
            code: data.returnCode.message
          };
          this.outValue.emit(parm);
          this.iscompele = false;
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          this.iscompele = false;
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        this.iscompele = false;
      });
    // } else {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'error', summary: '提示', detail: "编号必须是CHNL+6位数字!" });
    //   this.iscompele = false;
    // }
  }
  //取消
  addCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    }
    this.outValue.emit(parm);
  }

}

