import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ChannelManagementBean } from './../bean/channel-management.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { InputTextModule } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';

@Component({
  selector: 'app-channel-management-modify',
  templateUrl: './channel-management-modify.component.html',
  styleUrls: ['./../channel-management-add/channel-management-add.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class ChannelManagementModifyComponent implements OnInit, OnChanges {

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  userform: FormGroup; //校验
  siteType: any;//站点类型
  code: any;
  // bean
  channelManagementBean: ChannelManagementBean = new ChannelManagementBean();
  iscompele: boolean = false;
  //提示信息
  msgs: Message[] = [];

  constructor(public productManageHttpService: ProductManageHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc, ) {
    this.userform = fb.group({
      channelId: ['', Validators.required],
      channelName: ['', Validators.required],
      channelDescription: [''],
      serverCode: [''],
      systemCode: [''],
      situationCode: [''],
      channelType: ['', Validators.required],
    });
    this.codeValues() //调用方法，获取全部码值
    this.siteType = this.code['SiteType'];
    this.siteType.unshift({ 'label': '请选择', 'Value': '' });
    this.siteType = _.clone(this.siteType);
  }

  ngOnInit() {
    this.refresh();
    this.query();
  }
  ngOnChanges() {

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  refresh() {
    this.channelManagementBean = Object.assign({}, this.inValue);
  }

  query() {
    let parm = { channelId: this.inValue };
    this.productManageHttpService.productChannelDetail(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.channelManagementBean = data;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //修改
  modifyBtn() {
    this.iscompele = true;
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        this.iscompele = false;
        return;
      }
    }
    //提交服务
    this.productManageHttpService.productChannelUpdate(this.channelManagementBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        let parm = {
          display: false,
          code: data.returnCode.message
        }
        this.outValue.emit(parm);
        this.iscompele = false;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        this.iscompele = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.iscompele = false;
    })

  }
  //取消
  modifyCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    }
    this.outValue.emit(parm);
  }

}

