import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ChannelManagementBean } from './../bean/channel-management.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';

@Component({
  selector: 'app-channel-management-details',
  templateUrl: './channel-management-details.component.html',
  styleUrls: ['./../channel-management-add/channel-management-add.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class ChannelManagementDetailsComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;

  siteType: any;//站点类型
  code: any;
  // bean
  channelManagementBean: ChannelManagementBean = new ChannelManagementBean();

  //提示信息
  msgs: Message[] = [];

  creater: string = '';
  creatertime: string = '';
  laster: string = '';
  lastertime: string = '';

  constructor(public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc, ) {
    this.codeValues() //调用方法，获取全部码值
    this.siteType = this.code['SiteType'];
    this.siteType.unshift({ 'label': '请选择', 'Value': '' });
    this.siteType = _.clone(this.siteType);

  }

  ngOnInit() {

  }
  ngOnChanges() {
    this.query()
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  query() {
    let parm = { channelId: this.inValue };
    this.productManageHttpService.productChannelDetail(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.channelManagementBean = data;
        this.creater = data.createdUser;
        this.laster = data.lastUpdatedUser;
        let creatert = new Date(data.createdStamp);
        let createrts = new Date(data.lastUpdatedStamp);
        this.creatertime = this.commfunc.transDateN(creatert);
        this.lastertime = this.commfunc.transDateN(createrts);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }
}



