import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { ChannelManagementComponent } from './channel-management/channel-management.component';//渠道管理

const routes: Routes = [
    {
        path: 'channel-management', component: ChannelManagementComponent,
    },
    {
        path: 'product-launch', loadChildren: './product-launch/product-launch.module#ProductLaunchModule',
    },
];

export const ProductMarketRouting: ModuleWithProviders = RouterModule.forChild(routes);