import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { ConfirmationService } from 'primeng/primeng';//删除
import { ChannelManagementBean } from './bean/channel-management.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import * as _ from 'lodash';

@Component({
  selector: 'app-channel-management',
  templateUrl: './channel-management.component.html',
  styleUrls: ['./channel-management.component.scss'],
  providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 渠道站点-渠道管理
 */
export class ChannelManagementComponent implements OnInit {
  tabMesg: any = tableMessage;//列表无数据
  // bean
  channelManagementBean: ChannelManagementBean = new ChannelManagementBean();
  //头标题
  all_title: string = '渠道管理';
  //修改的参数
  updateValue: any = [];
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: string;
  first: number = 0;//分页控制
  //模态框
  showModel: string;
  display: boolean = false;
  //模态框的标题
  headerTitle: string;
  // 列表
  site_list: any = [];//渠道列表
  code: any;
  siteType: any;//渠道类型

  constructor(public productManageHttpService: ProductManageHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc) {
    this.codeValues() //调用方法，获取全部码值
    this.siteType = this.code['SiteType'];
  }

  ngOnInit() {
    this.queryClick();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  // 重置
  resetClick() {
    this.channelManagementBean = new ChannelManagementBean()
  }
  //查询始终第一页
  querySecle() {
    this.channelManagementBean.pageSize = 10;
    this.channelManagementBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  //  查询
  queryClick() {
    this.channelManagementBean.channelCode = this.channelManagementBean.channelId;
    this.productManageHttpService.productChannelList(this.channelManagementBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.site_list = data.productChannelList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //删除
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          channelId: item.channelId,
          toStatus: item.toStatus
        };
        this.productManageHttpService.productChannelDelete(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //新增
  addClick() {
    this.display = true;
    this.headerTitle = '新增';
    this.showModel = '1';
  }
  //新增的回调
  addCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  // 修改按钮
  modifyClick(item) {
    this.display = true;
    this.headerTitle = '修改';
    this.showModel = '2';
    this.updateValue = item.channelId;
  }
  //修改的回调
  updateCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  //详情
  detailClick(item) {
    this.display = true;
    this.headerTitle = '详情';
    this.showModel = '3';
    this.updateValue = item.channelId;
  }
  //详情的回调
  detailsCall(param) {
    this.display = param;
  }
  // //初始化渠道
  // intital(item) {
  //   this.display = true;
  //   this.headerTitle = '初始化渠道';
  //   this.showModel = '4';
  //   // this.updateValue = item.channelId;
  // }
  // //初始化渠道回调
  // intitalCall(param) {
  //   if (param.code == 'cancelModel') {
  //     this.display = param.display;
  //   } else {
  //     this.msgs = [];
  //     this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
  //     this.display = param.display;
  //   }
  //   this.first = 0;
  //   this.queryClick();
  // }
  //分页
  paginate(event) {
    // ;
    //每页显示的条数
    this.channelManagementBean.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.channelManagementBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.channelManagementBean.pageSize;
    //调用查询的方法
    this.queryClick();
    this.channelManagementBean.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
